.footer {
    
    background-color: #333333;

    .footer-inner {

        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 20px 0;
    
        min-height: initial;

        .footer-right {

            .right-bottom {
                .cayt-name {
                    color: white;
                    .sayt-span {
                        color: orange;
                    }
                }
            }

            .right-top {

                .svg-fill {
                    color: white;
                    margin-right:  6px;
                    margin-bottom: 5px;

                    &:hover {
                        fill: orange;
                    }
                }
            }
        }

        

        .footer-middle {
            display: flex;

            fill: white;

            .btr-link {
                display: flex;
                margin-bottom: 2px;
                margin-left: 30px;

                &:hover {
                    .svg-link {
                        color: orange;
                    }
                    .svg-fill {
                        fill: orange;
                    }
                }
                .svg-fill {
                    fill: white;
                }
        
                .svg-link {
                    color: white;
                    margin-left: 10px;
                }
            }

           
        }

        .footer-left {
            display: flex;

            .onelink {
                fill: white;
            }

            .last {
                display: block;
            }

            .svg-text1 {
                color: white;
            }
            
            .left-link {
                margin-right: 15px;
                display: flex;
                align-items: center;
                border-radius: 10px;
                border: 1px solid black;
                padding: 3px;
                background-color: black;
                &:hover {
                    box-shadow: 0 3px 3px 0 rgb(201, 135, 13);
                }
            }
        }
    }
}