.movie-card {
    width: 250px;
    max-width: 250px;
    // height: 400px;
    overflow: hidden;
    display: inline-block;
    position: relative;
    margin-bottom: 30px;
    &:hover {
        .card-title {
            color: white;
            // text-shadow: 0 0 3px white;
            font-size: 18px;
        }
    
        .card-date {
            color: white;
            // text-shadow: 0 0 3px white;
            font-size: 12px;
        }

        .card-title {
            color: white;
            // text-shadow: 0 0 3px white;
            font-size: 18px;
        }

        .card-rating {
            color: white;
        }
        
        
    
        .movie-img {
            object-fit: cover;
            width: 100%;
            transform: scale(1.3);
            overflow: hidden;
    
        }
    }

    .card-title {
        color: rgb(148, 144, 144);
        font-size: 18px;
        margin-top: 5px;
    }

    .card-date {
        color: rgb(148, 144, 144);
        font-size: 12px;
        margin-top: 10px;
    }
    
    

    .card-img-holder {
        object-fit: cover;
        width: 100%;
        // height: 150px;
        overflow: hidden;

        .movie-img {
            object-fit: cover;
            width: 100%;
            transition: all ease 0.6s;
        }

    }

    

    .card-rating {
        position: absolute;
        top: 30px;
        color: rgb(148, 144, 144);
        padding: 10px 20px 10px 10px;
        background-color: #333333;
        font-weight: bold;
        z-index: 1;
        right: 0;
    }

    .relisedate {
        color: rgb(148, 144, 144);
        font-size: 200px;
    }


}