.header {

    .one-spane {
        padding: 3px;
        background-color: white;
        padding: 0 5px;
        margin: 0 3px;
        color: black;
        transition: all ease 0.6s;
    }

    .two-spane {
        padding: 3px;
        background-color: orange;
        padding: 0 5px;
        margin: 0 3px;
        color: white;
        transition: all ease 0.9s;
    }

    .header-left  {
        &:hover {

        
        .two-spane {
            background-color: white;
            color: black;
        }

        .one-spane {
            background-color: orange;
            color: white;
        }
        .q {
            transition: all ease 0.1s;
        }
        .w {
            transition: all ease 0.2s;
        }
        .e {
            transition: all ease 0.3s;
        }
        .r {
            transition: all ease 0.4s;
        }
        .t {
            transition: all ease 0.5s;
        }
        .y {
            transition: all ease 0.6s;
        }
        .u {
            transition: all ease 0.7s;
        }
        .i {
            transition: all ease 0.8s;
        }
        .o {
            transition: all ease 0.9s;
        }
        .p {
            transition: all ease 1s;
        }
    }
    }

    .modal-input {
        position: absolute;
        // width: 0;
        // height: 0;
        z-index: -1;
        position: fixed;
        transition: all ease 0.2s;

        &.activ {
        z-index: 5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, 0.7);
        }

        
    }

    background-color: #333333;
    padding: 35px 0;

    .header-inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin: 0 auto;
        padding-left: 15px;
        padding-right: 15px;
        max-width: 1140px;
        box-sizing: border-box;
        position: relative;

        

        .input-div {

            .input {
                // width: 100%;
                position: absolute;
                left: 0;
                top: -10px;
                right: 0;
                margin: auto;
                width: 90%;
                padding: 10px;
                background: #333333;
                color: white;
                font-size: 18px;
                border: none;
                border-radius: 5px;
                transform: translateY(-1000px);
                outline: none;
                transition: all ease 0.5s;
                z-index: 6;
                &::placeholder {
                    color: white;
                }
                &.activ {
                    transform: translateY(0);
                    z-index: 6;
                    position: fixed;
                    max-width: 70%;
                    top: 20px;
                    border: 1px solid orange;
                }
            }
        }

        .search-icon {
            width: 30px;
            height: 30px;
        }

        .header-right {
            // position: relative;

            

            .btn {
                padding: 5px;
                color: black;
                cursor: pointer;
                
            }

            

            
        }

        .header-link{
            padding: 10px;
            color: white;
            text-decoration: none;
            padding: 10px 15px;
            // border: 1px solid red;
            margin: 0 2px;
            &:active {
                background: rgba(235, 9, 9, 0.6);
                color: black;
            }
            &:hover {
                background: rgba(187, 165, 165, 0.6);
                color: black;
            }

        }

        .header-logotip {
            display: flex;
            align-items: center;

            .logotip {
                color: red;
                text-decoration: none;

                .logo {
                    color: white;
                    text-decoration: none;
                }
            }
        }
    }
}